
const GeoService = {
	currentCoordinates() {
		let geolocationSupported = 'geolocation' in navigator;

		return new Promise((resolve, reject) => {
			navigator.geolocation.getCurrentPosition(
				({ coords }) => resolve(coords),
				error => reject(error),
			);
		});
	}
};

export default GeoService;


